@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.header{height:100vh;width:100%;font-size:24px;overflow:hidden}.header::after{content:"";background:url(/Portfolio/static/media/Header.84974038.jpg) right no-repeat;opacity:0.6;top:0;left:0;bottom:0;right:0;position:absolute;z-index:-1;overflow:hidden}.header-title{position:relative;top:0;left:15%;font-size:2.5em;color:#45505b;font-weight:700;line-height:56px}.header-title .subtitle{font-size:0.5em;font-family:"Popins", sans-serif;color:#0563bb;overflow:hidden;border-right:.1em solid #0563bb;white-space:nowrap;letter-spacing:.15em;-webkit-animation:typing 5.5s steps(26, start),caret 0.7s step-end infinite;animation:typing 5.5s steps(26, start),caret 0.7s step-end infinite}.header-title .social-links{font-size:2rem;color:#45505b;margin-top:15px}.header-title .social-links .icon{color:#45505b;margin-right:20px;-webkit-transition:0.3s;transition:0.3s}.header-title .social-links .icon:hover{color:#0563bb}@-webkit-keyframes typing{from{width:0}to{width:100%}}@keyframes typing{from{width:0}to{width:100%}}@-webkit-keyframes caret{from,to{border-color:transparent}50%{border-color:#0563bb}}@keyframes caret{from,to{border-color:transparent}50%{border-color:#0563bb}}@media (max-width: 767px){.header-title{text-align:center;left:0;font-size:1.3em;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}

.nav-menu{justify-self:flex-start;position:fixed;z-index:9999}.nav-menu .navbar-links .icon{font-size:22px}.nav-menu .navbar-links .link{display:flex;align-items:center;padding:10px 18px;margin-bottom:15px;border-radius:50px;background:#f2f3f5;height:56px;width:56px;overflow:hidden;-webkit-transition:0.4s;transition:0.4s;color:#45505b}.nav-menu .navbar-links .link:hover{text-decoration:none;background-color:#0563bb;color:#f2f3f5}.nav-menu .navbar-links .link:hover{width:100%}.nav-menu .navbar-links .link:hover span{display:inline-block;margin-left:15px}.nav-menu .navbar-links .link span{display:none}.nav-menu .navbar-mobile{display:none}@media (max-width: 768px){.nav-menu{position:absolute;top:0;right:0;width:100vw;background-color:#0563bb}.nav-menu .navbar-desktop{display:none}.nav-menu .navbar-mobile{top:0;right:0;display:block;z-index:9998;font-size:1.5rem;-webkit-transition:all 0.4s;transition:all 0.4s;outline:none !important;cursor:pointer;overflow:hidden}.nav-menu .navbar-mobile .navbar-toggler{border-color:#A3BDE0}.nav-menu .navbar-mobile .navbar-nav{background-color:#0563bb;height:100vh;display:flex;justify-content:center}.nav-menu .navbar-mobile .navbar-nav .nav-link{color:#fff}}

.section-title{padding:30px 0;font-size:32px;font-weight:700;margin-bottom:60px;position:relative;color:#45505b}.section-title::before{content:'';position:absolute;display:block;width:120px;height:1px;background:#ddd;bottom:1px;left:calc(50% - 60px)}.section-title::after{content:'';position:absolute;display:block;width:40px;height:3px;background:#0563bb;bottom:0;left:calc(50% - 20px)}

.small-title{padding-bottom:30px;font-size:32px;font-weight:700;margin-bottom:30px;color:#0563bb}

#about{width:90%}.image{width:190px;height:190px;-webkit-filter:opacity(0.8);filter:opacity(0.8)}.intro{font-size:1.5em}.intro .arrow{color:#0563bb;font-size:20px;padding-right:2px;margin-right:6px}@media (max-width: 767px){.intro{font-size:1em}}

.skills-stack{padding:40px;margin:0 20px}.skills-stack .skills{width:100px}.skills-stack .icons{font-size:64px;padding:2px}.skills-stack .html{color:#E44D26}.skills-stack .css{color:#2262AF}.skills-stack .sass{color:#DA6999}.skills-stack .bootstrap{color:#674096}.skills-stack .js{color:#EFD81E}.skills-stack .react{color:#5DD2F3}.skills-stack .node{color:#659A60}.skills-stack .db{color:#818181}.skills-stack .git{color:#E94E31}.skills-stack .npm{color:#C53635}.skills-stack .yarn{color:#2F8AB5}.skills-stack svg{width:60px;height:60px}

.card{width:35%;box-shadow:2px 2px 20px #444;-webkit-transition:0.4s;transition:0.4s}.card .link{color:#0563bb}.card .link:hover{text-decoration:none;background-color:#0563bb;color:#fff}.card .img{width:100%;height:70%}.card .card-body{min-height:250px;display:flex;justify-content:center;align-items:center;flex-direction:column}@media (max-width: 991px){.card{width:100%;height:100%}}@media (min-width: 992px) and (max-width: 1199px){.card{height:100%}.card .img{height:300px}}

.contact{padding:60px 0 20px}.contact .icons{display:flex;align-items:center;padding:10px 18px;margin-bottom:15px;border-radius:50px;background:#f2f3f5;height:56px;width:56px;overflow:hidden;-webkit-transition:0.4s;transition:0.4s;background-color:#0563bb;color:#f2f3f5}.contact .icons:hover{text-decoration:none;background-color:#0563bb;color:#f2f3f5}.contact .back-to-top{position:absolute;bottom:10px;right:10px;background-color:#0563bb;color:#f2f3f5;padding:10px 18px;margin-bottom:8px;border-radius:50px;border:none;height:56px;width:56px}@media (max-width: 767px){.address{width:70%}.phone{width:70%}.email{width:70%}}

.section{padding:60px 0 0;font-family:'Montserrat', sans-serif;width:100%}.section-bg{background-color:#F7F8F9}

